html,
body {
  margin: 0px;
  padding: 0px;
  height: 100%;
}
html {
  background: linear-gradient(
    to bottom,
    rgb(3, 77, 114) 1%,
    rgb(113, 49, 99) 100%
  );
}
body {
  width: 100%;
}
.scene {
  width: 100%;
  height: 100%;
  animation: focus 10s ease-in-out infinite;
  -webkit-filter: blur(5px);
  z-index: 1;
  position: absolute;
}
@keyframes focus {
  0% {
    -webkit-filter: blur(0px);
  }
  30% {
    -webkit-filter: blur(0px);
  }
  50% {
    -webkit-filter: blur(4px);
  }
  60% {
    -webkit-filter: blur(0px);
  }
  80% {
    -webkit-filter: blur(0px);
  }
  90% {
    -webkit-filter: blur(10px);
  }
  100% {
    -webkit-filter: blur(0px);
  }
}
.ufoWrap {
  display: table;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 3;
}
.center {
  display: table-cell;
  vertical-align: middle;
}

